:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
  --frame-default-color: black;
  --frame-dice-width: 50px;
  --frame-dice-height: 80px;
  --font-dice-face: 40px;
  --font-dice-max: 18px;
  --inactive-color: #414141;
}

.button {
  cursor: pointer;
}

.button:hover {
  transform: scale(1.1);
}

.diceBag {
  max-width: 1140px;
  grid-template-columns: repeat( auto-fit, minmax(var(--frame-dice-width), var(--frame-dice-width) ) );
  grid-auto-rows: var(--frame-dice-height);
  grid-gap: 8px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

.dice {
  cursor: pointer;
  border: 3px solid;
  border-color: var(--frame-default-color);
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
  border-radius: 20%;
  padding: 0 5px;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.dice:hover {
  transform: scale(1.1);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.dice__max {
  font-size: var(--font-dice-max);
}

.dice__rolled {
  font-size: var(--font-dice-face);
}

.button-end-turn {
  cursor: pointer;
  border: 3px solid;
  border-color: var(--frame-default-color);
  border-radius: 20%;
}

.inactive {
  background-color: var(--inactive-color) !important;
}

.inactive:hover {
  cursor: not-allowed;
  transform: scale(1);
}

